
















export default {
	data() {
		return {
			news : []
		}
	},
	
	methods: {
		openinfo(e){
			// console.log(e);
			var newsid = e.currentTarget.dataset.newsid;
			// console.log(newsid);
			uni.navigateTo({
				url: '../info/info?newsid='+newsid,
				success: res => {
					// console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onLoad:function(){
		uni.showLoading({
			title:"加载中......"
		})
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
